.App {
  /* font-family: 'Abel', sans-serif; */
  font-family: 'Roboto', sans-serif;
  text-align: center;
  /* background-image: linear-gradient(-90deg, #A94C4C, #501B6A);
   */
  background-image: url('https://github.com/PCMACHero/signup/blob/master/public/bg-vacation.jpeg?raw=true');
  background-size: cover;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}



@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}


.sign-up-container{
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: rgba(255, 255, 255, 0.742);
  width: 800px;
  height: 500px;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 5px 5px   5px rgba(0, 0, 0, 0.673);
}
.sign-up-box{
  /* border: red solid 1px; */
  width: 400px;
  height: 450px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.s-title{
  font-family: 'Oswald', sans-serif;
  /* height: 34px; */
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 45px;
}

.s-greeting{
  height: 28px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Roboto', sans-serif;
}
.input-box{
  display: -webkit-flex;
  display: flex;
  width: 100%;
  min-height: 50%;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.input{
  width: 100%;
  
}
input[type=email], input[name=signuppassword]{
  background-color: rgba(3, 0, 41, 0.714);
  color: rgb(255, 255, 255);
  margin: 2px;
  padding: 5px;
  border: 1px solid rgba(104, 104, 104, 0.434);
  border-radius: 5px;
  height: 2em;
  width: 80%;
  font-size: 20px;
}

input[type=email]:focus, input[name=signuppassword]:focus{
  outline: 4px solid rgba(128, 0, 128, 0.475);
  
  margin: 2px;
  padding: 5px;
  /* box-shadow: rgb(79, 19, 109); */
  height: 2em;
  width: 80%;
}
.input-message{
  font-family: 'Abel', sans-serif;
}
.agree-text{
  font-size: 10px;
}

